// ==========================================================================
// Base Containers
// ==========================================================================

*, *:before, *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

// Containers
.container, .container-full {
  padding-left: $grid-gutter;
  padding-right: $grid-gutter;
  margin-left: auto;
  margin-right: auto;
}
// Rows
.row {
  position: relative;
  margin-left: -$grid-gutter;
  margin-right: -$grid-gutter;
}

@include breakpoint(m) {
  // Rows - Tablet
  .row-m {
    position: relative;
    margin-left: -$grid-gutter;
    margin-right: -$grid-gutter;
  }
  // Containers - Tablet
  .container-m, .container-full-m {
    padding-left: $grid-gutter;
    padding-right: $grid-gutter;
    margin-left: auto;
    margin-right: auto;
  }
  .container, .container-m { width: $container-m; }
  .container-full-m { width: auto; }
}

@include breakpoint(l) {
  // Rows - Desktop
  .row-l {
    position: relative;
    margin-left: -$grid-gutter;
    margin-right: -$grid-gutter;
  }
  // Containers - Desktop
  .container-l, .container-full-l {
    padding-left: $grid-gutter;
    padding-right: $grid-gutter;
    margin-left: auto;
    margin-right: auto;
  }
  .container, .container-l { width: $container-l; }
  .container-full-l { width: auto; }
}

@include breakpoint(xl) {
  // Rows - Extra Large Devices
  .row-xl {
    position: relative;
    margin-left: -$grid-gutter;
    margin-right: -$grid-gutter;
  }
  // Containers - Extra Large Devices
  .container-xl, .container-full-xl {
    padding-left: $grid-gutter;
    padding-right: $grid-gutter;
    margin-left: auto;
    margin-right: auto;
  }
  .container, .container-xl { width: $container-xl; }
  .container-full-xl { width: auto; }
}